.create-project-box {
  position: absolute;
  box-sizing: border-box;
  border: 0px solid #000000;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.25), 12px 12px 12px rgba(0, 0, 0, 0.25);
  border-radius: 18px;
  width: 80%;
  height: fit-content;
  padding: 5%;
  top: 12%;
  right: 0;
  left: 0;
  bottom: 50%;
  margin: auto;
}

.cp-top {
  position: relative;
  top: -10%;
  font-size: 60%;
  text-align: center;
}

.cp-description {
  position: relative;
  top: 20%;
  margin-right: 5%;
  font-size: 80%;
  text-align: justify;
}

.cp-description > ul {
  padding-left: 12px;
}

.cp-input-section {
  position: relative;
  top: 50%;
}

.cp-input-section > small {
  font-size: 10px;
  margin-left: 5px;
}

.cp-input-titles {
  position: relative;
  font-size: 16px;
}

.cp-skills-section {
  display: grid;
  grid-template-columns: repeat(3, 3fr);
  grid-auto-rows: 2rem;
  grid-gap: 1.5rem;
  width: 100%;
}

.cp-skills-section .cp-skill-buttons > button {
  background-color: black;
  color: white;
  width: 100%;
  height: fit-content;
}

.cp-skills-section .cp-skill-buttons > button:hover {
  background-color: red;
  color: white;
  border-color: red;
}

.cp-input-section > input {
  position: relative;
  border-radius: 6px;
  border: 1px solid #000000;
  width: 89%;
  height: 50px;
  font-size: 1rem;
  font-family: 'Nunito';
  padding: 5px;
}

.cp-input-section > select {
  background-color: white;
  border-radius: 6px;
  border: 1px solid #000000;
  height: 63px;
  padding: 5px;
  margin-right: 5px;
  font-family: 'Nunito';
}

.cp-input-checkbox > input {
  position: relative;
  border-radius: 6px;
  border: 1px solid #000000;
  font-size: 1rem;
  padding: 5px;
}

.cp-input-section .input-amount {
  width: 20%;
  font-size: 1rem;
}

.cp-input-section > textarea {
  resize: vertical;
  border-radius: 6px;
  background: white;
  border: 1px solid #000000;
  box-sizing: border-box;
  width: 90%;
  height: 8rem;
  font: 1rem 'Nunito';
  padding: 5px;
 }

 .cp-button-section {
   position: relative;
   margin: 0;
   bottom: -5%;
   top: 30px;
   width: 100%;
   text-align: center;
 }

.cp-button-section > button {
  position: relative;
  margin: 10px;
}

.cp-edit-button {
  position: absolute;
  top: 130px;
  left: 200px;
}

.cp-summary-sections > h4,
.cp-summary-sections > p,
.cp-summary-sections > h2,
.cp-summary-sections > button {
  display: inline;
}

.cp-summary-sections > h2 {
  text-decoration: underline;
}

@media (max-width: 650px) {
  .create-project-box {
    top: 50px;
    width: 100%;
    border: 0;
    box-shadow: 0 0 0 0;
  }

  .cp-input-section > input {
    font-size: 0.8rem;
    width: 92%;
  }

  .cp-input-section > textarea {
    font-size: 0.8rem;
    width: 95%;
    height: 10rem;
  }

  .cp-button-section {
    top: 10px;
  }
}

@media (max-width: 380px) {
  .create-project-box {
    top: 60px;
  }
}
