.pv-box {
  display: block;
  text-align: center;
  position: fixed;
  width: 12vw;
  max-height: 80%;
  margin-left: 16vw;
  top: 9rem;
  padding: 0.5rem;
  box-shadow: 0 2px 5px grey;
  background: #C4C4C4;
  border-radius: 28px;
  background-color: white;
  overflow: hidden;
  z-index: 1;
}

.pv-box button {
  margin-left: auto;
  margin-right: auto;
}

.pv-box button:focus {
  background-color: white;
  color: black;
}

.pv-box h4 {
  margin: 5px;
  margin-left: auto;
  margin-right: auto;
}

.pv-box-list-item {
  box-sizing: border-box;
  margin-top: 1px;
  margin-bottom: 1px;
  padding: 1rem;
  width:auto;
  height: auto;
  background-color: #EEEEEE;
  border: 1px solid lightgray;
  border-radius: 18px;
}

.pv-profile-picture {
  width: 6rem;
  height: 6rem;
  border: 2px solid black;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 180px;
  background-color: black;
}

.pv-profile-picture img {
  width: 100%;
  height: 100%;
  border-radius: 180px;
  object-fit: cover;
}

.pv-no-picture {
  position: absolute;
  color: white;
  margin-top: 16px;
  margin-left: 16px;
  text-align: center;
  font-size: 14px;
}

.pv-no-picture button {
  padding: 0.1rem 0.1rem;
  font-size: 12px;
}

.pv-menu-wrapper {
position: relative;
margin-left: auto;
margin-right: auto;
}

.pv-menu {
  position: absolute;
  width: 80%;
  top: 1vh;
  left: 1.6vw;
  box-shadow: 0 2px 12px grey;
  background: #C4C4C4;
  background-color: white;
  border-radius: 18px;
}

.pv-menu:before {
    position: absolute;
    left: 40%;
    top: -10px;
    width: 0;
    height: 0;
    content: '';
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid white;
  }

.pv-menu-button1 {
  width: 100%;
  font: inherit;
  cursor: pointer;
  padding: 0.01rem 0.5rem;
  transition: all 0.5s ease;
  border: 1px solid white;
  background-color: #EEEEEE;
  color: black;
  border-radius: 18px 18px 0px 0px;
}

.pv-menu-button2 {
  width: 100%;
  font: inherit;
  cursor: pointer;
  padding: 0.01rem 0.5rem;
  transition: all 0.5s ease;
  border: 1px solid white;
  background-color: #EEEEEE;
  color: black;
  border-radius: 0px 0px 18px 18px;
}

.pv-menu-button1:hover,
.pv-menu-button2:hover
 {
  color: white;
  background-color: black;
}


.pv-bio {
  width:auto;
  text-align: left;
}

.pv-bio-box {
  height: 50px;
  width: auto;
  background-color: #EEEEEE;
  border-radius: 18px;
  overflow: hidden;
  text-overflow: clip;
  word-break: break-all;
  white-space: pre-wrap;
}

.pv-bio-box p {
  padding-top: 6px;
  padding-left: 8px;
  padding-right: 8px;
  font-size: 13px;
  font-family: inherit;
  width: 90%;
}

.pv-links {
  text-align: left;
  height: auto;
  background-color: white;
  border-radius: 18px;
}

.pv-links-icons {
  text-align: center;
  font-size: 3vh;
  margin-left: -10px;
}

.pv-links i {
  font-size: 3.5vh;
  margin-left: 10px;
}

i.fa-product-hunt {
font-size: 3.2vh;
}

.pv-links i:hover {
  font-size: 3.6vh;
}

i.fa-product-hunt:hover {
font-size: 3.3vh;
}

.pv-projects {
  text-align: left;
}

.pv-projects-box {
  height: 50px;
  width:auto;
  background-color: #EEEEEE;
  border-radius: 18px;
}

@media (max-width: 900px) {
  .pv-box {
    top: 9rem;
    margin-left: 5vw;
    width: 15vw;
  }
}

@media (max-width: 650px) {

  .pv-box {
    position: fixed;
    width: 98%;
    max-height: 10%;
    bottom: 0;
    left: 0;
    top: auto;
    margin-left: 0px;
    padding: 0.5rem;
    box-shadow: 0 2px 5px grey;
    background: #C4C4C4;
    border-radius: 0px;
    background-color: white;
    overflow: visible;
    z-index: 100;
  }

  .pv-profile-picture {
    position: absolute;
    width: 4rem;
    height: 4rem;
    top: 5px;
    float: left;
  }

  .pv-box > h4 {
    top: -5px;
  }

  .pv-box-items > button {
    position: absolute;
    right: 12vw;
    top: 10px;
    width: 50px;
    height: 50px;
    -moz-transform:rotate(-90deg);
    -ms-transform:rotate(-90deg);
    -o-transform:rotate(-90deg);
    -webkit-transform:rotate(-90deg);
    box-shadow: 0 0 0 0;
   }

   .pv-box-items {
     height: 5px;
   }

   .pv-menu-wrapper {
     position: relative;
     margin-left: auto;
     margin-right: auto;
   }

   .pv-menu {
     position: absolute;
     width: 25%;
     top: -112px;
     left: 70vw;
     box-shadow: 0 2px 12px grey;
     background: #C4C4C4;
     background-color: white;
     border-radius: 18px;
   }

   .pv-menu:before {
     position: absolute;
     left: 41%;
     top: 48px;
     width: 0;
     height: 0;
     content: '';
     border-left: 10px solid transparent;
     border-right: 10px solid transparent;
     border-top: 10px solid white;
     border-bottom: 0px;
   }

   .pv-menu-button1 {
     width: 100%;
     font: inherit;
     cursor: pointer;
     padding: 0.01rem 0.5rem;
     transition: all 0.5s ease;
     border: 1px solid white;
     background-color: #EEEEEE;
     color: black;
     border-radius: 18px 18px 0px 0px;
   }

   .pv-menu-button2 {
     width: 100%;
     font: inherit;
     cursor: pointer;
     padding: 0.01rem 0.5rem;
     transition: all 0.5s ease;
     border: 1px solid white;
     background-color: #EEEEEE;
     color: black;
     border-radius: 0px 0px 18px 18px;
   }

   .pv-menu-button1:hover,
   .pv-menu-button2:hover
    {
     color: white;
     background-color: black;
   }

   .pv-bio {
     display: none;
   }

   .pv-bio-box {
     display: none;
   }

   .pv-links {
     display: none;
   }

   .pv-projects {
     display: none;
   }

   .pv-projects-box {
     display: none;
   }
}

@media (max-width: 480px) {
  .pv-menu {
    position: absolute;
    width: 30%;
    top: -112px;
    left: 68vw;
    box-shadow: 0 2px 12px grey;
    background: #C4C4C4;
    background-color: white;
    border-radius: 18px;
  }

  .pv-no-picture {
    display: none;
  }

  .pv-no-picture button {
    display: none;
  }

  .pv-menu:before {
    position: absolute;
    left: 41%;
    top: 48px;
    width: 0;
    height: 0;
    content: '';
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid white;
    border-bottom: 0px;
  }

  .pv-box-items > button {
    right: 11vw;
  }
}

@media (max-width: 380px) {
  .pv-menu-wrapper {
    position: relative;
    margin-left: auto;
    margin-right: auto;
  }

  .pv-menu {
    position: absolute;
    width: 25%;
    top: -157px;
    left: 70vw;
    box-shadow: 0 2px 12px grey;
    background: #C4C4C4;
    background-color: white;
    border-radius: 18px;
  }

  .pv-menu:before {
    position: absolute;
    left: 40%;
    top: 92px;
    width: 0;
    height: 0;
    content: '';
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid white;
    border-bottom: 0px;
  }
}
